"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
// ... (other imports)
import { DashboardHome } from "@/components/dashboard-home"
import { Menu, Star, X } from "lucide-react"

// --- Placeholder Components (kept for reference) ---
interface AddRatingFormProps {
  gameId?: string;
  onRatingAdded: () => void;
  onCancel: () => void;
}
const AddRatingForm = ({ gameId, onRatingAdded, onCancel }: AddRatingFormProps) => (
  <div className="p-6 border rounded-lg space-y-4">
    <h2 className="text-2xl font-bold">Add New Rating {gameId && `for Game ID: ${gameId}`}</h2>
    <p className="text-muted-foreground">This is a placeholder for your AddRatingForm component.</p>
    <div className="flex gap-2">
      <Button onClick={onRatingAdded}>Submit Rating (Placeholder)</Button>
      <Button variant="outline" onClick={onCancel}>Cancel</Button>
    </div>
  </div>
);

interface AddClipFormProps {
  gameId?: string;
  onClipAdded: () => void;
  onCancel: () => void;
}
const AddClipForm = ({ gameId, onClipAdded, onCancel }: AddClipFormProps) => (
  <div className="p-6 border rounded-lg space-y-4">
    <h2 className="text-2xl font-bold">Add New Clip {gameId && `for Game ID: ${gameId}`}</h2>
    <p className="text-muted-foreground">This is a placeholder for your AddClipForm component.</p>
    <div className="flex gap-2">
      <Button onClick={onClipAdded}>Submit Clip (Placeholder)</Button>
      <Button variant="outline" onClick={onCancel}>Cancel</Button>
    </div>
  </div>
);
// ---------------------------------------------------


interface DashboardProps {
  user: {
    username: string;
    email: string;
    userid: string; // UUID string
    // ðŸ”‘ CHANGE 1: Assume isDev is a mandatory property on the user object
    isDev: boolean;
  }
  onLogout: () => void
}

export function Dashboard({ user, onLogout }: DashboardProps) {
  const [view, setView] = useState<"home" | "browse" | "add" | "characters" | "maps" | "mobs" | "ratings" | "add-rating" | "account" | "clips" | "add-clip">("home")

  // ðŸ”‘ CHANGE 2: Initialize role to 'viewer'. No need for a separate role loading state.
  const [role, setRole] = useState<"dev" | "editor" | "viewer">("viewer")

  const [games, setGames] = useState<any[]>([])
  // ... (other data states)
  const [characters, setCharacters] = useState<any[]>([])
  const [maps, setMaps] = useState<any[]>([])
  const [mobs, setMobs] = useState<any[]>([])
  const [ratings, setRatings] = useState<any[]>([])
  const [clips, setClips] = useState<any[]>([])

  // The state structure for holding the ID
  const [gameIdForRating, setGameIdForRating] = useState<string | null>(null)
  const [gameIdForClip, setGameIdForClip] = useState<string | null>(null)

  // ... (loading states)
  const [loadingGames, setLoadingGames] = useState(true)
  const [loadingCharacters, setLoadingCharacters] = useState(true)
  const [loadingMaps, setLoadingMaps] = useState(true)
  const [loadingMobs, setLoadingMobs] = useState(true)
  const [loadingRatings, setLoadingRatings] = useState(true)
  const [loadingClips, setLoadingClips] = useState(true)

  const [menuOpen, setMenuOpen] = useState(false)

  useEffect(() => {
    // ðŸ”‘ CHANGE 3: Set role immediately based on the user prop, which is already fetched.
    if (user.isDev) {
      setRole("dev")
    } else {
      // You could add logic here to differentiate 'editor' from 'viewer' if needed,
      // but based on the prompt, 'dev' is the only special case for adding games.
      setRole("viewer")
    }

    fetchGames()
    fetchCharacters()
    fetchMaps()
    fetchMobs()
    fetchRatings()
    fetchClips()

    // ðŸ”‘ Dependency array now only includes user.isDev and component lifecycle functions
  }, [user.isDev])


  // --- (Your existing fetch and handler functions remain the same) ---
  // ...

  const fetchGames = async () => { /* ... */ setLoadingGames(false); }
  const handleGameAdded = () => { /* ... */ }
  const fetchCharacters = async () => { /* ... */ setLoadingCharacters(false); }
  const handleCharacterAdded = () => { /* ... */ }
  const fetchMobs = async () => { /* ... */ setLoadingMobs(false); }
  const handleMobAdded = () => { /* ... */ }
  const fetchMaps = async () => { /* ... */ setLoadingMaps(false); }
  const handleMapAdded = () => { /* ... */ }
  const fetchRatings = async () => {
    // Fixed the bug in the map function here
    try { /* ... */ } catch (err) { /* ... */ } finally { setLoadingRatings(false); }
  }
  const handleRatingAdded = () => { /* ... */ }
  const handleInitiateAddRating = (gameId: string | undefined) => { setGameIdForRating(gameId || null); setView("add-rating"); }
  const fetchClips = async () => { /* ... */ setLoadingClips(false); }
  const handleClipAdded = () => { /* ... */ }
  const handleInitiateAddClip = (gameId: string | undefined) => { setGameIdForClip(gameId || null); setView("add-clip"); }


  const navItems = [
    { id: "home", label: "Dashboard" },
    { id: "browse", label: "Browse Games" },
    // Conditional display of "Add Game" based on the role state
    ...(role === "dev" ? [{ id: "add", label: "Add Game" }] : []),
    { id: "ratings", label: "Game Ratings", icon: Star },
    { id: "clips", label: "Clips" },
    { id: "characters", label: "Characters" },
    { id: "maps", label: "Maps" },
    { id: "mobs", label: "Mobs" },
  ]

  const displayName = user.username && user.username.length > 0
    ? user.username
    : (user.email ? user.email.split('@')[0] : 'Account');

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <header className="bg-primary text-primary-foreground border-b-4 border-accent">
        <div className="max-w-7xl mx-auto px-4 py-4 flex justify-between items-center">

          <button
            onClick={() => setView("home")}
            className="flex items-center gap-2 hover:opacity-80 transition-opacity"
          >
            <div className="text-2xl font-bold">CRIMSON DB</div>
          </button>

          <div className="flex items-center gap-4">

            <button
              onClick={() => { setView("account"); setMenuOpen(false); }}
              className="text-sm underline hover:opacity-80 transition-opacity"
            >
              {displayName} ({role})
            </button>

            <button onClick={() => setMenuOpen(!menuOpen)} className="p-2 hover:bg-primary/80 rounded">
              {menuOpen ? <X size={24} /> : <Menu size={24} />}
            </button>
          </div>
        </div>

        {/* Dropdown Navigation Menu */}
        {menuOpen && (
          <div className="bg-primary/95 border-t border-primary-foreground">
            <div className="max-w-7xl mx-auto px-4 py-4">
              <div className="flex flex-col md:flex-row md:items-center md:gap-2 w-full space-y-2 md:space-y-0">
                {navItems.map((item) => (
                  // The navItems array already handles hiding 'Add Game' based on role
                  <Button
                    key={item.id}
                    onClick={() => {
                      setView(item.id as any)
                      setMenuOpen(false)
                    }}
                    className="w-full justify-start md:w-auto md:justify-center"
                    variant={view === item.id ? "default" : "outline"}
                  >
                    {item.label}
                  </Button>
                ))}

                <Button
                  onClick={onLogout}
                  className="w-full justify-start bg-transparent md:w-auto md:justify-center md:ml-auto"
                  variant="outline"
                >
                  Logout
                </Button>
              </div>
            </div>
          </div>
        )}
      </header>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto p-4">
        {view === "account" && <AccountDetails user={user} onBack={() => setView("home")} />}

        {/* Pass the derived role state to DashboardHome */}
        {view === "home" && <DashboardHome games={games} characters={characters} maps={maps} mobs={mobs} onNavigate={setView} userRole={role} />}

        {view === "browse" && <GamesGallery games={games} loading={loadingGames} onRefresh={fetchGames} />}

        {/* Conditional Rendering: Only render AddGameForm if role is 'dev' */}
        {view === "add" && role === "dev" && <AddGameForm onGameAdded={handleGameAdded} />}
        {view === "add" && role !== "dev" && (
          <div className="text-center p-10 text-xl text-red-500">Permission Denied: Only developers can add new games.</div>
        )}

        {view === "ratings" && <RatingsBrowser ratings={ratings} loading={loadingRatings} onRefresh={fetchRatings} onAddRating={() => handleInitiateAddRating(undefined)} />}
        {view === "add-rating" && (
          <AddRatingForm
            gameId={gameIdForRating || undefined}
            onRatingAdded={handleRatingAdded}
            onCancel={() => setView("ratings")}
          />
        )}

        {view === "characters" && <CharacterBrowser characters={characters} loading={loadingCharacters} onRefresh={fetchCharacters} onCharacterAdded={handleCharacterAdded} />}
        {view === "maps" && <MapBrowser maps={maps} loading={loadingMaps} onRefresh={fetchMaps} onMapAdded={handleMapAdded} />}
        {view === "mobs" && <MobBrowser mobs={mobs} loading={loadingMobs} onRefresh={fetchMobs} onMobAdded={handleMobAdded} />}

        {view === "clips" && <ClipBrowser clips={clips} loading={loadingClips} onRefresh={fetchClips} onAddClip={() => handleInitiateAddClip(undefined)} />}
        {view === "add-clip" && (
          <AddClipForm
            gameId={gameIdForClip || undefined}
            onClipAdded={handleClipAdded}
            onCancel={() => setView("clips")}
          />
        )}
      </main>
    </div>
  )
}